.App {
  text-align: center;
}


.modal-content {
  position: absolute;
  top: 40px;
  left: 40px;
  right: 40px;
  bottom: 40px;
  border: 1px solid rgb(204, 204, 204);
  background: rgb(255, 255, 255);
  overflow: auto;
  border-radius: 4px;
  outline: none;
  padding: 20px;
}

.modal-close {
  /* close button on the modal */
  position: absolute;
  right: 16px;
  top: 16px;
  width: 32px;
  height: 32px;
  background: url("/images/ui/close.png");
  cursor: pointer;
}